.swiper,
.swiper_second {
  width: 830px;
  margin-left: 16px !important;
  overflow: hidden;
  margin-bottom: 16px; }

.swiper-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  background-color: inherit; }

.slide {
  width: 240px;
  height: 72px;
  display: flex;
  border: 1px solid #eaeaea;
  border-radius: 4px;
  background-color: #fff;
  flex-shrink: 0;
  margin: 0 30px 16px 0; }

.slide a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: #1b1c21; }

.swiper-pagination {
  position: relative !important; }

.swiper-pagination-bullet {
  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 12px)) !important;
  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 12px)) !important;
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 6px) !important; }

.swiper-pagination-bullet-active {
  --swiper-theme-color: #757575; }

.slide__img {
  width: 130px;
  height: 45px;
  background-repeat: no-repeat;
  background-position: 0 50%;
  margin-left: 16px; }

.slide__btn {
  width: 40px;
  height: 40px;
  background-image: url(./static/arrow_btn.svg);
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  border: 2px solid #ff3e79;
  border-radius: 50%;
  margin-right: 16px;
  flex-shrink: 0; }

.slide__second {
  width: 240px;
  height: 160px;
  display: flex; }

.slide__second a {
  align-items: normal; }

.slide__second .slide__btn {
  bottom: -100px; }

.slide__txt {
  margin-left: 16px;
  margin-top: 16px;
  height: 24px;
  font-weight: 600; }

.slide_third {
  width: 260px;
  height: 200px;
  display: flex; }

.slide__txt_title {
  margin-top: 0;
  margin-bottom: 0;
  color: #7e7e82;
  font-size: 14px;
  font-weight: normal; }

.slide__txt_description {
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: normal; }

.swiper_third {
  max-width: 1100px;
  margin-left: 8px !important;
  overflow: hidden;
  margin-bottom: 16px; }
